html {
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

*,
::before,
::after {
  -webkit-box-sizing: inherit;
          box-sizing: inherit; }

body {
  margin: 0;
  font-size: 15px;
  line-height: 1.2;
  font-weight: 400;
  font-family: "Lato", Arial, Helvetica, sans-serif;
  color: #3d3d3d;
  background-color: #f3f3f3; }

.header {
  color: #fff;
  background-color: #222222; }

.container {
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px; }

.content__container {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex; }

.content__aside {
  -ms-flex-negative: 0;
      flex-shrink: 0;
  width: 297px;
  margin-right: 39px; }

.aside__menu {
  margin: 0; }

.aside__block:not(:first-child) {
  margin-top: 30px; }

.info-block {
  background-color: #fff;
  -webkit-box-shadow: 0 0 7px rgba(0, 0, 0, 0.03);
          box-shadow: 0 0 7px rgba(0, 0, 0, 0.03); }

.inner-menu {
  list-style: none;
  padding: 0 14px 6px; }

.inner-menu__link {
  display: block;
  padding: 16px 9px 16px 43px;
  color: #222222;
  text-decoration: none; }
  .inner-menu__link:hover {
    color: #f10e34; }

.inner-menu__item:not(:last-child) .inner-menu__link {
  border-bottom: 1px solid #eaeaea; }

.widget {
  padding-left: 25px;
  padding-right: 25px; }

.widget__top {
  padding: 17px 0 17px;
  border-bottom: 1px solid #eaeaea; }

.widget__title {
  margin: 0; }

.title {
  font-size: 22px;
  line-height: 1.36;
  font-weight: 700;
  text-transform: uppercase;
  color: #000; }
